import React, { createContext, useState, useEffect, useContext } from 'react';
import axios from 'axios';

// To Context
const CartContext = createContext();

// Provider  cung cp trng thi gi hng cho cc component con
export const CartProvider = ({ children }) => {
  const [cartCount, setCartCount] = useState(0);

  // Hm  ly d liu t API
  const fetchCartCount = async () => {
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_URL}/cart`,{withCredentials: true}); // ng dn API ly s lng sn phm trong gi hng
      setCartCount(response.data.count);
    } catch (error) {
      console.error('Failed to fetch cart count:', error);
    }
  };

  useEffect(() => {
    fetchCartCount(); 
  }, []);

  const addToCart = async () => {
    try {
      setCartCount(cartCount + 1); 
    } catch (error) {
      console.error('Failed to add to cart:', error);
    }
  };
  const removeFromCart = async()=>{
    try {
      setCartCount(cartCount-1);
    } catch (error) {
      console.error(error);
    }
  };
  const pay = async()=>{
    try {
      setCartCount(0);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <CartContext.Provider value={{ cartCount, addToCart, removeFromCart, pay }}>
      {children}
    </CartContext.Provider>
  );
};

// Hook  s dng CartContext
export const useCart = () => useContext(CartContext);
